.slide-enter {
    opacity: 0.01;
}

.slide-enter.slide-enter-active {
    opacity: 1;
    transition: opacity 300ms ease-in;
}

.slide-exit {
    opacity: 1;
}

.slide-exit.slide-exit-active {
    opacity: 0.01;
    transition: opacity 300ms ease-out;
}


//Slide down like a slot machine